.JokeList {
  display: flex;
  width: 80%;
  height: 80%;
}

.JokeList-sidebar {
  background: rgba(240, 98, 146, 1);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 30%;
  text-align: center;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1),
    inset 0 0 25px rgba(245, 102, 151, 1);
  z-index: 2;
}

.JokeList-title {
  font-size: 3rem;
  margin: 2rem;
  color: white;
  font-weight: 300;
  letter-spacing: 0.6rem;
}

.JokeList-title span {
  font-weight: 700;
  letter-spacing: 0;
  color: white;
}
.terrible {
  color: yellow !important;
  font-size: 3rem;
}

.JokeList-sidebar img {
  width: 50%;
}

.JokeList-jokes {
  height: 90%;
  background: white;
  align-self: center;
  width: 70%;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
}

.JokeList-getmore {
  padding: 0.5rem 1.8rem;
  margin: 1.5rem;
  border-radius: 10rem;
  color: white;
  background: #9575cd;
  border: 1px solid white;
  font-size: 1rem;
}
