.Joke {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 400;
  padding: 1rem;
}
.joker {
  font-size: 1rem;
}

.Joke-buttons {
  display: flex;
  margin-right: 1rem;
  align-items: center;
  justify-content: center;
  width: 15%;
}

.Joke-text {
  margin: 5rem auto;
  margin-top: 15rem;
  text-align: center;
  width: 75%;
  font-size: 2rem;
  background: rgb(0, 120, 254);
  color: white;
  padding: 1.5rem;
  border-radius: 3rem;
}

.Joke-smiley {
  font-size: 3rem;
  margin-left: auto;
  border-radius: 50%;
  box-shadow: 0 10px 38px rgba(0, 0, 0, 0.2), 0 10px 12px rgba(0, 0, 0, 0.1);
}
